* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.loader {
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}

iframe {
    display: block; 
    width: 100%; 
    height: 100vh;
    border: none; 
    overflow-y: auto; 
    overflow-x: hidden;
}